@import url(https://fonts.googleapis.com/css2?family=Dosis:wght@700&family=Readex+Pro&family=Roboto:wght@400;700&display=swap);
.tabs{display:flex;justify-content:center;grid-gap:10px;gap:10px}.tabs__button{height:30px;border-radius:8px;font-weight:bold;border:1px solid #7c2923;background-color:#cf3f34;color:#fff;transition:0.3s}.tabs__button:hover{background-color:#f57d75}

body,h1{margin:0}html{font-family:"Readex Pro", sans-serif;background-color:#626367;color:#fff}.App__container{width:600px;margin:0 auto}.App__title{margin:20px;text-align:center;font-size:24px}.App__content{margin:20px;text-align:center;font-size:32px}

